































































































































import Vue from "vue";

import { stripLeadingSlash } from "@/lib";

import Checkbox from "@/components/Checkbox.vue";
import Help from "@/components/Help.vue";
import Results from "@/components/Results.vue";
import TextInput from "@/components/TextInput.vue";

import { markdownImageWithLink } from "@/core/badges";
import { mkHtmlImg } from "@/core/images";

// 'Image' is reserved in Vue so use alternative.
export default Vue.extend({
  name: "ImageView",
  components: {
    Checkbox,
    Help,
    Results,
    TextInput,
  },
  data() {
    return {
      imgSrc: "/sample.png",
      linkTarget: "",
      altText: "Sample screenshot",
      imgTitle: "",
      width: "400",
      height: "",
      alignCenter: true,
      result: "_Your output will appear here_",
    };
  },
  computed: {
    altTextOut(): string {
      return stripLeadingSlash(this.altText || this.imgSrc);
    },
    imgTitleOut(): string {
      // If neither is set, then an empty string is fine to pass in.
      return stripLeadingSlash(this.imgTitle || this.altText);
    },
  },
  methods: {
    submit() {
      console.debug("Process inputs and render results");

      const htmlImg = mkHtmlImg(
          this.imgSrc,
          this.altTextOut,
          this.imgTitleOut,
          this.linkTarget,
          this.width,
          this.height,
          this.alignCenter ? "center" : ""
        ),
        mdImg = markdownImageWithLink(
          this.altTextOut,
          this.imgSrc,
          this.linkTarget,
          this.imgTitleOut
        );

      this.result = `\
_HTML image_

${htmlImg}

_Markdown image_

${mdImg}
      `;
    },
  },
});
